import * as React from 'react';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';

export default {
  name: 'mention',

  match: (node: JsonML): boolean => {
    const tag = getTagName(node);
    const attrs = getAttributes(node);
    return tag === 'span' && !!attrs && attrs['data-type'] === 'mention';
  },

  convert: (node: JsonML): React.ReactElement => {
    // @ts-ignore
    const { id, name, login } = getAttributes(node);
    return (
      <React.Fragment>
        <span data-type="mention" data-id={id} data-login={login}>@{name}</span>
      </React.Fragment>
    );
  },
};
